import React, { useRef, useLayoutEffect, useEffect, useState } from "react";

import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
import mapDataFile from "../data/projects";

am4core.useTheme(am4themes_animated);

export default function Map(props) {
  const chartContainer = useRef(null);
  const [ps, setPs] = useState(null);
  const [longs, setLongs] = useState({});
  const [lats, setLats] = useState({});
  const [allLocs, setAllLocs] = useState([]);
  const [realAllLocs, setRealAllLocs] = useState([]);
  const [ft, setFt] = useState(true);
  const setLocs = (longitudes, latitudes, aL) => {
    if (
      Object.keys(longitudes).length >= 19 &&
      Object.keys(latitudes).length === Object.keys(longitudes).length &&
      aL.length === 0
    ) {
      let locs = [];
      for (let name in longitudes) {
        locs.push({
          longitude: longitudes[name],
          latitude: latitudes[name],
          name: name,
        });
      }

      setAllLocs(locs);
    }
  };

  useLayoutEffect(() => {
    let chart = am4core.create("chartdiv", am4maps.MapChart);

    if (mapDataFile && mapDataFile.length) {
      const typesArr = [...new Set(mapDataFile.map((project) => project.type))];

      let types = {};

      typesArr.forEach((type, index) => {
        types[type] = index;
      });

      const mapData = mapDataFile.map((project) => {
        project["id"] = project["countryCode"];
        project["color"] = chart.colors.getIndex(types[project.type]);
        project["value"] = 123123;
        return project;
      });

      // Set map definition
      chart.geodata = am4geodata_worldLow;

      // Set projection
      chart.projection = new am4maps.projections.Miller();

      // Create map polygon series
      var polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());
      polygonSeries.exclude = ["AQ"];

      polygonSeries.useGeodata = true;
      polygonSeries.nonScalingStroke = true;
      polygonSeries.strokeWidth = 0.5;
      polygonSeries.calculateVisualCenter = true;

      polygonSeries.events.on("validated", function () {
        imageSeries.invalidate();
      });

      var imageSeries = chart.series.push(new am4maps.MapImageSeries());
      imageSeries.data = mapData;
      imageSeries.dataFields.value = "value";

      var imageTemplate = imageSeries.mapImages.template;
      imageTemplate.nonScaling = true;

      imageTemplate.adapter.add("latitude", function (latitude, target) {
        var polygon = polygonSeries.getPolygonById(
          target.dataItem.dataContext.id
        );
        if (polygon) {
          const name = target.dataItem.dataContext.name;

          setLats((l) => {
            l[name] = polygon.visualLatitude;
            return l;
          });
          setLocs(longs, lats, allLocs);
          return polygon.visualLatitude;
        }
        const name = target.dataItem.dataContext.name;
        setLats((l) => {
          l[name] = latitude;
          return l;
        });

        setLocs(longs, lats, allLocs);
        return latitude;
      });

      imageTemplate.adapter.add("longitude", function (longitude, target) {
        var polygon = polygonSeries.getPolygonById(
          target.dataItem.dataContext.id
        );
        if (polygon) {
          const name = target.dataItem.dataContext.name;
          setLongs((l) => {
            l[name] = polygon.visualLongitude;
            return l;
          });
          setLocs(longs, lats, allLocs);
          return polygon.visualLongitude;
        }
        const name = target.dataItem.dataContext.name;
        setLongs((l) => {
          l[name] = longitude;
          return l;
        });
        setLocs(longs, lats, allLocs);
        return longitude;
      });

      var circle = imageTemplate.createChild(am4core.Circle);
      circle.fillOpacity = 0.7;
      circle.propertyFields.fill = "color";
      circle.tooltipText = "{country}: {name}";

      imageTemplate.events.on("hit", function (ev) {
        props.setCurrentProject(ev.target.dataItem.dataContext);
      });

      imageSeries.heatRules.push({
        target: circle,
        property: "radius",
        min: 10,
        max: 10,
        dataField: "value",
      });

      chartContainer.current = chart;
      setPs(polygonSeries);
      props.setMapdata(mapData);
    }

    return () => {
      chart.dispose();
    };
  }, []);

  useEffect(() => {
    if (allLocs.length >= 19 && realAllLocs.length === 0) {
      console.log("all", allLocs);

      let lineSeries = chartContainer.current.series.push(
        new am4maps.MapArcSeries()
      );
      lineSeries.mapLines.template.strokeWidth = 4;
      lineSeries.mapLines.template.stroke = am4core.color("#e03e96");
      let geoLines = [];

      allLocs.forEach((r1) => {
        allLocs.forEach((r2) => {
          const rr1 = mapDataFile.find((e) => e.name === r1.name);
          const rr2 = mapDataFile.find((e) => e.name === r2.name);
          console.log("hola", rr1, rr2);
          if (
            rr1.type === rr2.type &&
            rr1.countryCode !== rr2.countryCode &&
            rr1.type === "Supporting and Expanding Promising SRH Innovations"
          ) {
            geoLines.push([
              { longitude: r1.longitude, latitude: r1.latitude },
              { longitude: r2.longitude, latitude: r2.latitude },
            ]);
          }
        });
      });
      console.log("geolines", geoLines);

      // lineSeries.data = [
      //   {
      //     multiGeoLine: geoLines,
      //   },
      // ];
      setRealAllLocs(allLocs);
    }
  }, [allLocs]);

  useEffect(() => {
    if (ft) {
      setFt(false);
    }
  }, [realAllLocs]);

  useEffect(() => {
    if (props.currentProject === null) {
      chartContainer.current.goHome();
    } else {
      const p = ps.getPolygonById(props.currentProject.countryCode);

      chartContainer.current.zoomToMapObject(p, 5);
    }
  }, [props.currentProject]);

  return <div id='chartdiv' style={{ width: "100%", height: "90vh" }}></div>;
}
