import React, { useState, useEffect } from "react";
import Map from "./Components/Map";
import ProjectCard from "./Components/ProjectCard";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import { Modal } from "react-bootstrap";
import ReactCardCarousel from "react-card-carousel";

function App() {
  const [currentProject, setCurrentProject] = useState(null);
  const [mapdata, setMapdata] = useState(null);
  const [carr, setCarr] = useState(null);

  const sliderChangeHandler = () => {
    if (carr && mapdata) {
      const index = carr.getCurrentIndex();
      const cP = mapdata[index - 1] || mapdata[0];
      console.log("cambiando", index, cP);
      setCurrentProject(cP);
    }
  };

  return (
    <div className='App'>
      <Map
        setCurrentProject={setCurrentProject}
        currentProject={currentProject}
        setMapdata={setMapdata}
      />

      {currentProject && (
        <Modal
          show={currentProject !== null && currentProject !== undefined}
          onHide={() => setCurrentProject(null)}
        >
          <ReactCardCarousel
            autoplay={false}
            afterChange={sliderChangeHandler}
            ref={(reff) => setCarr(reff)}
          >
            <div>
              <ProjectCard currentProject={currentProject} />
            </div>
            {mapdata &&
              mapdata.length &&
              mapdata.map((project, index) => {
                return (
                  <div key={index}>
                    <ProjectCard currentProject={project} />
                  </div>
                );
              })}
          </ReactCardCarousel>
        </Modal>
      )}
    </div>
  );
}

export default App;
